body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #1c1c1c;
  color: #f0f0f0;
}

.game-list-wrapper {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #2b2b2b;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0,0,0,0.6);
}

.page-title {
  font-size: 1.8rem;
  color: #f5c518;
  border-bottom: 1px solid #444;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

.game-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.game-list li {
  padding: 0.75rem 0;
  border-bottom: 1px solid #444;
}

.game-list li:last-child {
  border-bottom: none;
}

.game-list a {
  color: #f0f0f0;
  font-weight: 500;
  transition: color 0.2s;
}

.game-list a:hover {
  color: #f5c518;
  text-decoration: underline;
}
